<template>
  <div class="visual">
    <h2>Simple Visual Component</h2>
    <div class="box"></div>
  </div>
</template>

<script>
export default {
  name: 'VisualComponent'
}
</script>

<style scoped>
.visual {
  text-align: center;
  margin-top: 20px;
}

.box {
  width: 100px;
  height: 100px;
  background-color: #42b983;
  margin: 0 auto;
  animation: spin 4s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
</style>